import React from 'react';
import {Container, Row, Col} from 'reactstrap';

import logoHyperium from '../images/hyperium-large.png';
import arrowDropDown from '../images/arrow-drop-down.svg';
import './Header.css';

class Header extends React.Component {

  render() {
    return <header>
      <Container className="h-100">
        <Row className="h-100">
          <Col className="my-auto text-center text-white">
            <h1>Hyperium</h1>
            <p className="lead">
              Hyperium is a Free and Open-source Minecraft Client with HUDs and other popular mods
            </p>
          </Col>
        </Row>
      </Container>
      <div class="scroll-down">
        <a href="#about">
          <img src={arrowDropDown} width="50"/>
        </a>
      </div>
    </header>;
  }

}

export default Header;



